@import url(https://fonts.googleapis.com/css2?family=Merriweather:wght@700&family=Montserrat:wght@300;400;500;600;700&display=swap);
form {
    display: flex;
    justify-content: center;
    margin-bottom: 1rem;
}

input[type="text"] {
    border: none;
    background-color: #f4f3ee;
    height: 3.2rem;
    padding: 0 1rem;
    border-radius: 4px;
    width: 100%;
    font-size: 16px; /* set to 16px to prevent zoom in on input focus on mobile  */
}

/* @media all and (min-width: 768px) {
   input[type="text"] {
       font-size: 1rem;
       background-color: #fff;
   }
  } */
.resultContainer {
    width: 95%;
    display: flex;
    justify-content: space-between;
    margin-bottom: .5rem;
    border-bottom: .5px solid #bcb8b1;
}

.resultTitle {
    font-size: .75rem;
    font-weight: 500;
    margin: 0;
}
.linkButton {
    background: transparent;
    color: #5F0A87;
    border: none;
    text-decoration: underline;
    font-size: 1rem;
}

.successAlert {
    background: transparent;
    color: green;
    border: none;
    font-weight: 700;
}


@media all and (min-width: 600px) {
    .cardContainer {
        width: 100%;
    }
    .linkButton, .successAlert {
        font-size: 1rem;
    }
    
}





.libraryContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

@media all and (min-width: 601px) and (max-width: 800px) {
  .libraryContainer {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 1rem;
    gap: 1rem;
    margin: 0 auto;
  }
}


@media screen and (min-width: 801px) {
    .libraryContainer {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-gap: .25rem;
      gap: .25rem;
      margin: 0 auto;
  }
}


@media (min-width: 600px) {
    nav {
        font-size: 2rem;
    }
}

nav {
    display: flex;
    justify-content: center;
    font-size: 1.5rem;
}

.navLink {
    margin: 1rem;
    vertical-align: text-bottom;
    text-decoration: none;
    color: #BCB8B1;
}
body {
    margin: 0;
    font-family: 'Montserrat', -apple-system, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 62.5%;
    
  }

  .homeContainer {
      width: 80%;
      margin: 0 auto;
  }

.buttonPrimary {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    background: #5F0A87;
    padding: .5rem 1rem;
    color: #fff;
    border: none;
    border-radius: 2px;
}

/* Book Item and Result Card */
.cardContainer {
    display: flex;
    width: 100%;
    justify-content: space-between;
    border-bottom: .5px solid #bcb8b1;
    margin-bottom: .25rem;
    background: #fff;
}
.card {
    display: flex;
    flex-direction: row;
    border: none;
}
.title {
    font-weight: 700;
    font-size: .9rem;
    margin: 0;
    font-family: 'Merriweather', serif;
    text-transform: capitalize;
}
.coverContainer {
    max-height: 6rem;
    max-width: 4rem;
    overflow: hidden;
    padding: 0 0 .25rem 0;
}
.coverImage {
    height: 100%;
}
.author {
    font-weight: 400;
    font-size: .7rem;
    margin-bottom: .25rem;
}
.identifiers {
    font-size: .65rem;
    font-weight: 300;
    margin: 0;
}
.bookInfo {
    margin: .5rem;
    max-width: 13rem;
}

@media all and (min-width: 768px) {
    body {
        background: #F6F5F4;
    }
    .aboutContainer {
        margin: 3rem auto;
    }
    .homeHeader {
        font-size: 3rem;
        
    }
    .homeAbout {
        font-size: 1rem;
    }
    .cardContainer {
        width: 19rem;
        box-shadow: 1px 1px 10px 1px rgba(0,0,0,.1);
        border: none;
    }
    .coverContainer {
        padding: 0;
    }
    .title {
        font-size: 1rem;
    }
    main {
        max-width: 95%;
    }
    input[type="text"] {
        font-size: 1rem;
        background-color: #fff;
    }
    nav {
        font-size: 2rem;
    }
}



